import { BoardController, BoardView } from "@swim/panel";
import { ControllerRef, TraitViewRef } from "@swim/controller";
import { Model, Trait } from "@swim/model";
import { ViewRef } from "@swim/view";
import { PanelView } from "@swim/panel";
import { TableTrait, TextColTrait } from "@swim/ux";
import { Look } from "@swim/theme";
import { LeaderboardTableController } from "./LeaderboardTableController";

export class LeaderboardController extends BoardController {
  constructor() {
    super();

    const boardView = this.sheet.attachView();
    const panelView = this.panel.insertView(boardView);
    const tablePanel = this.tablePanel.insertView(panelView);

    // create table model; append header and column traits to model
    const tableModel = new Model();
    tableModel.mount();
    const tableTrait = new TableTrait();
    tableModel.setTrait("table", tableTrait);
    tableTrait.header.insertTrait();
    tableTrait.appendTrait(TextColTrait, "rank").set({
      layout: { key: "rank", grow: 1, textColor: Look.labelColor },
      label: "Rank",
    });
    tableTrait.appendTrait(TextColTrait, "name").set({
      layout: { key: "name", grow: 3 },
      label: "Player",
    });
    tableTrait.appendTrait(TextColTrait, "level").set({
      layout: { key: "level", grow: 1, textColor: Look.labelColor },
      label: "Level",
    });
    // this.levelColTrait.setTrait(levelColTrait);
    tableTrait.appendTrait(TextColTrait, "team").set({
      layout: { key: "team", grow: 1, textColor: Look.labelColor },
      label: "Team",
    });
    tableTrait.appendTrait(TextColTrait, "kd").set({
      layout: { key: "kd", grow: 3, textColor: Look.labelColor },
      label: "KD Ratio",
    });

    // mount tableController; set its model; insert its view
    this.tableController.attachController().mount();
    this.tableController.attachController().tableModel.set(tableModel);
    this.tableController.attachController().table.insertView(tablePanel);
    this.tableController.attachController().table.setTrait(tableTrait);
  }

  @TraitViewRef({
    extends: true,
    createView(): BoardView {
      const mainElement = document.createElement("main");
      const boardView = new BoardView(mainElement).set({
        style: {
          width: "100%",
          flexGrow: 1,
          flexShrink: 1,
          flexBasis: "0px",
          margin: "0px",
          backgroundColor: "#212121",
        },
      });

      return boardView;
    },
  })
  override readonly sheet!: TraitViewRef<this, Trait, BoardView> & BoardController["sheet"];

  @ViewRef({
    viewType: PanelView,
    initView(panelView: PanelView) {
      panelView.set({
        classList: ["panel-leaderboardController"],
      });
      this.owner.tablePanel.insertView(panelView);
    },
  })
  readonly panel!: ViewRef<this, PanelView>;

  @ViewRef({
    extends: true,
    createView(): PanelView {
      return PanelView.create();
    },
    initView(panelView: PanelView): void {
      super.initView(panelView);
      panelView.node.classList.add("main-controller__panel-view");
      panelView.node.style.backgroundColor = "#181818";
      panelView.node.style.overflowY = "scroll";
      panelView.set({
        unitWidth: 1,
        unitHeight: 1,
      });
    },
  })
  readonly tablePanel!: ViewRef<this, PanelView>;

  @ControllerRef({
    controllerType: LeaderboardTableController,
    extends: true,
  })
  readonly tableController!: ControllerRef<this, LeaderboardTableController>;
}
