import { TraitViewRef } from "@swim/controller";
import { TraitRef } from "@swim/model";
import {
  CellTrait,
  CellView,
  LeafTrait,
  LeafView,
  RowController,
  RowTrait,
  RowView,
  TextCellTrait,
  TextCellView,
} from "@swim/table";
import { Observes } from "@swim/util";
import { Property } from "@swim/component";
import { PlayerChange, PlayerDetails } from "../types";
import { Uri } from "@swim/uri";
import { MapDownlink } from "@swim/client";
import { Value } from "@swim/structure";
import { HtmlView } from "@swim/dom";
import { ViewRef } from "@swim/view";
import { KdSeriesView } from "./KdSeriesView";

export class LeaderboardRowController extends RowController {
  private _resetChangeStateTimer: NodeJS.Timeout | null = null;

  constructor(trait: RowTrait) {
    super();
    this.trait.set(trait);

    const urlParams = new URLSearchParams(window.location.search);

    let host = urlParams.get("host");
    const baseUri = Uri.parse(document.location.href);
    if (!host) {
      host = baseUri
        .base()
        .withScheme(baseUri.schemeName === "https" ? "warps" : "warp")
        .toString();
    }
    this.statsHistoryDownlink.setHostUri(host);
  }

  @TraitViewRef({
    viewType: RowView,
    extends: true,
    initView(rowView: RowView): void {
      super.initView(rowView);
      rowView.set({
        style: {
          height: "44px",
        },
      });
    },
  })
  override readonly row!: TraitViewRef<this, RowTrait, RowView>;

  @TraitViewRef({
    viewType: LeafView,
    extends: true,
    initView(leafView: LeafView): void {
      super.initView(leafView);
      leafView.set({
        style: {
          height: "44px",
        },
      });

      if (Number.parseInt(this.owner.key ?? "0", 10) % 2 === 1) {
        leafView.set({
          style: {
            backgroundColor: "#242424",
          },
        });
      }
    },
    viewDidPress(): void {
      // disable default press action
      return;
    },
    viewDidLongPress(): void {
      // disable default long press action
      return;
    },
  })
  override readonly leaf!: TraitViewRef<this, LeafTrait, LeafView> & Observes<LeafTrait> & Observes<LeafView>;

  @TraitRef({
    traitType: RowTrait,
    extends: true,
  })
  readonly trait!: TraitRef<this, RowTrait>;

  @TraitViewRef({
    traitType: TextCellTrait,
    viewType: TextCellView,
    initView(view): void {
      super.initView(view);
      view.set({
        classList: ["rankCell"],
        style: {
          fontSize: "14px",
          color: "#FBFBFB",
          opacity: 0.8,
        },
      });
    },
    extends: true,
  })
  readonly rankCell!: TraitViewRef<this, TextCellTrait, TextCellView>;

  @TraitViewRef({
    traitType: TextCellTrait,
    viewType: TextCellView,
    initView(view): void {
      super.initView(view);
      view.set({
        classList: ["nameCell"],
        style: {
          fontSize: "14px",
          color: "#FBFBFB",
          opacity: 0.8,
        },
      });
    },
    extends: true,
  })
  readonly nameCell!: TraitViewRef<this, TextCellTrait, TextCellView>;

  @TraitViewRef({
    traitType: TextCellTrait,
    viewType: TextCellView,
    initView(view): void {
      super.initView(view);
      view.set({
        classList: ["levelCell"],
        style: {
          fontSize: "14px",
          color: "#FBFBFB",
          opacity: 0.8,
        },
      });
    },
    extends: true,
  })
  readonly levelCell!: TraitViewRef<this, TextCellTrait, TextCellView>;

  @TraitViewRef({
    traitType: TextCellTrait,
    viewType: TextCellView,
    initView(view): void {
      super.initView(view);
      view.set({
        classList: ["teamCell"],
        style: {
          color: "#FBFBFB",
          opacity: 0.8,
          justifyContent: "center",
        },
      });
      const firstChild = view.firstChild;
      if (firstChild) {
        view.removeChild(firstChild);
      }
      this.owner.teamTeamSpan.insertView(view, undefined, null, "teamTeamSpan");
      this.owner.teamPctSpan.insertView(view, undefined, null, "teamPctSpan");
    },
    extends: true,
  })
  readonly teamCell!: TraitViewRef<this, TextCellTrait, TextCellView>;

  @ViewRef({
    viewType: TextCellView,
    createView(): HtmlView {
      return new HtmlView(document.createElement("span")).set({
        classList: ["teamTeamSpan"],
        style: {
          display: "inline-flex",
          justifyContent: "center",
          alignItems: "center",
          marginRight: "4px",
          marginBottom: "4px",
          fontSize: "14px",
        },
      });
    },
  })
  readonly teamTeamSpan!: ViewRef<this, HtmlView>;

  @ViewRef({
    viewType: TextCellView,
    createView(): HtmlView {
      return new HtmlView(document.createElement("span")).set({
        classList: ["teamPctSpan"],
        style: {
          display: "inline-flex",
          justifyContent: "center",
          alignItems: "center",
          marginTop: "4px",
          fontSize: "12px",
        },
      });
    },
  })
  readonly teamPctSpan!: ViewRef<this, HtmlView>;

  @TraitViewRef({
    traitType: CellTrait,
    viewType: CellView,
    initView(view): void {
      super.initView(view);
      view.set({
        classList: ["kdCell"],
        style: {
          fontSize: "14px",
          color: "#FBFBFB",
          opacity: 0.8,
        },
      });

      const firstChild = view.firstChild;
      if (firstChild) {
        view.removeChild(firstChild);
      }
      this.owner.kdCellSeries.insertView(view, undefined, null, "kdCellSeries");
      this.owner.kdCellText.insertView(view, undefined, null, "kdCellText");
    },
    extends: true,
  })
  readonly kdCell!: TraitViewRef<this, CellTrait, CellView>;

  @ViewRef({
    viewType: KdSeriesView,
    createView(): KdSeriesView {
      return new KdSeriesView();
    },
  })
  readonly kdCellSeries!: ViewRef<this, KdSeriesView>;

  @ViewRef({
    viewType: TextCellView,
    createView(): HtmlView {
      return new HtmlView(document.createElement("span")).set({
        classList: ["kdCellText"],
        style: {
          display: "inline-flex",
          justifyContent: "center",
          alignItems: "center",
          marginLeft: "4px",
        },
      });
    },
  })
  readonly kdCellText!: ViewRef<this, HtmlView>;

  @Property({
    valueType: Number,
    didSetValue(newValue): void {
      const nodeUri = `/player/${newValue}`;

      if (this.owner.statsHistoryDownlink.opened) {
        const oldKdCellSeries = this.owner.kdCell.attachView().getChild("kdCellSeries", KdSeriesView);
        if (oldKdCellSeries) {
          // remove current kd time series view
          this.owner.kdCell.attachView().removeChild("kdCellSeries");

          // add new, empty kd time series view
          const newKdCellSeries = this.owner.kdCell.attachView().prependChild(KdSeriesView, "kdCellSeries");
          this.owner.kdCellSeries.setView(newKdCellSeries);
          if (this.owner.changeState.value !== "none") {
            this.owner.kdCellSeries.attachView().updateStroke(this.owner.changeState.value as PlayerChange);
          }
        }
        this.owner.statsHistoryDownlink.close();
      }
      this.owner.statsHistoryDownlink.setNodeUri(nodeUri);
      // re-open downlink; results in empty time series being populated with the data for the new playerId
      this.owner.statsHistoryDownlink.open();
    },
  })
  readonly playerId!: Property<this, Number>;

  @Property({
    value: "none",
    valueType: String,
    didSetValue(newValue, oldValue) {
      if (newValue !== "none") {
        this.owner.row.attachView().classList.remove(newValue === "rising" ? "falling" : "rising");
        this.owner.row.attachView().classList.add(newValue.toString());
        this.owner.kdCellSeries.attachView().updateStroke(newValue.toString() as PlayerChange);

        if (this.owner._resetChangeStateTimer !== null) {
          clearTimeout(this.owner._resetChangeStateTimer!);
        }
        this.owner._resetChangeStateTimer = setTimeout(() => {
          this.owner.row.attachView().classList.remove(newValue.toString());
          this.owner.kdCellSeries.attachView().updateStroke("none");
          this.owner.changeState.setValue("none");
        }, 2500);
      }
    },
  })
  readonly changeState!: Property<this, String>;

  updateRow(item: PlayerDetails, rank: string, change: PlayerChange = "none"): void {
    const playerId = item.id;
    const name = item.tag;
    const level = item.level;
    const kills = item.totalKills;
    const deaths = item.totalDeaths;
    const kd = deaths === 0 ? `${kills}.00` : (kills / deaths).toFixed(2);
    const d = item.teamCount.Donut;
    const h = item.teamCount.Hubble;

    // update content
    this.rankCell.attachTrait().set({
      content: rank,
    });
    this.nameCell.attachTrait().set({
      content: name,
    });
    this.levelCell.attachTrait().set({
      content: level.toFixed(0),
    });

    this.teamTeamSpan.attachView().node.innerText = d >= h ? "DON" : "HUB";
    let pctText = `${Math.round((d >= h ? d / (d + h) : h / (d + h)) * 100)}%`;
    if ((d ?? 0) === 0 || (h ?? 0) === 0) {
      pctText = "100%";
    }
    this.teamPctSpan.attachView().node.innerText = pctText;

    this.kdCellText.attachView().node.innerText = kd;

    if (change !== "none") {
      this.changeState.setValue(change);
    }

    this.playerId.setValue(playerId);
  }

  @MapDownlink({
    laneUri: "statsHistory",
    didUpdate(key, newValue) {
      const timestamp = key.numberValue();
      const kdRatio = newValue.get("kdRatio").numberValue();
      this.owner.kdCellSeries.attachView().addPoint(timestamp, kdRatio);
    },
  })
  readonly statsHistoryDownlink!: MapDownlink<this, Value>;
}
