import { ValueDownlink } from "@swim/client";
import { TraitViewControllerSet, TraitViewRef } from "@swim/controller";
import {
  RowController,
  RowTrait,
  RowView,
  TableController,
  TableTrait,
  TableView,
  TextCellTrait,
  TextCellView,
} from "@swim/table";
import { Observes } from "@swim/util";
import { LeaderboardRowController } from "./LeaderboardRowController";
import { Record as SwimRecord } from "@swim/structure";
import { Uri } from "@swim/uri";
import { Model } from "@swim/model";
import { Property } from "@swim/component";
import { PlayerDetails, PlayerChange } from "../types";

export class LeaderboardTableController extends TableController {
  LEADERBOARD_SIZE = 20;

  constructor() {
    super();
    LeaderboardTableController.initFasteners(this);
  }

  protected override didMount(): void {
    const urlParams = new URLSearchParams(window.location.search);

    let host = urlParams.get("host");
    const baseUri = Uri.parse(document.location.href);
    if (!host) {
      host = baseUri
        .base()
        .withScheme(baseUri.schemeName === "https" ? "warps" : "warp")
        .toString();
    }

    // set up and open status downlink
    this.leaderboardDownlink.setHostUri(host);
    this.leaderboardDownlink.open();
  }

  @Property({
    valueType: Model,
  })
  readonly tableModel!: Property<this, Model>;

  @TraitViewRef({
    extends: true,
    traitWillAttachRow(rowTrait, targetTrait) {
      this.owner.rows.addTrait(rowTrait, targetTrait, rowTrait.key);
    },
  })
  override readonly table!: TraitViewRef<this, TableTrait, TableView> &
    Observes<TableTrait> &
    Observes<TableView>;

  @TraitViewControllerSet({
    extends: true,
    controllerDidEnterLeafView(leafView) {
      leafView.hover.focus(false);
    },
    controllerDidLeaveLeafView(leafView) {
      leafView.hover.unfocus(false);
    },
    controllerDidPressLeafView(input, event, leafView) {
      leafView.highlight.toggle();
    },
    createController(trait) {
      const traitKey = trait?.key ?? "";

      if (trait && traitKey) {
        const leaderboardRowController = new LeaderboardRowController(trait);
        leaderboardRowController.setKey(traitKey);
        return leaderboardRowController;
      }

      return super.createController(trait);
    },
  })
  override readonly rows!: TraitViewControllerSet<this, RowTrait, RowView, LeaderboardRowController> &
    Observes<RowController> &
    TableController["rows"];

  createLeaderboardRowController(rank: string, item: PlayerDetails): void {
    const rowModel = new Model();
    const rowTrait = new RowTrait();
    rowModel.setTrait(rank, rowTrait);

    // Create cells in trait before appending to model to display being set to 'none'
    const rankCell = rowTrait.getOrCreateCell("rank", TextCellTrait);
    const nameCell = rowTrait.getOrCreateCell("name", TextCellTrait);
    const levelCell = rowTrait.getOrCreateCell("level", TextCellTrait);
    const teamCell = rowTrait.getOrCreateCell("team", TextCellTrait);
    const kdCell = rowTrait.getOrCreateCell("kd", TextCellTrait);

    this.tableModel.value.appendChild(rowModel);

    const newLeaderboardRowController = Object.values(this.rows.controllers).find((c) => c?.key === rank) as
      | LeaderboardRowController
      | undefined;

    if (newLeaderboardRowController) {
      newLeaderboardRowController.playerId.setValue(item.id);

      newLeaderboardRowController.rankCell.setTrait(rankCell);
      newLeaderboardRowController.nameCell.setTrait(nameCell);
      newLeaderboardRowController.levelCell.setTrait(levelCell);
      newLeaderboardRowController.teamCell.setTrait(teamCell);
      newLeaderboardRowController.kdCell.setTrait(kdCell);
      newLeaderboardRowController.updateRow(item, rank);

      ["rank", "name", "level", "team", "kd"].forEach(function (key) {
        const view = Object.values(
          newLeaderboardRowController.row.attachView().leaf.attachView().cells.views
        ).find((v) => v?.key === key) as TextCellView | undefined;
        if (view) {
          (
            newLeaderboardRowController[`${key}Cell` as "rankCell"] as TraitViewRef<
              LeaderboardRowController,
              TextCellTrait,
              TextCellView
            >
          ).setView(view, null, key);
        }
      });
    }
  }

  @ValueDownlink({
    nodeUri: "/player",
    laneUri: `leaderboard`,
    consumed: true,
    didSet(value: SwimRecord): void {
      let items = Object.values(value.toObject() as Record<any, PlayerDetails>);
      let removedPlayers = new Set<Number>();

      items.forEach((item, index) => {
        const rank = (index + 1).toFixed(0);

        let rowController = this.owner.getChild(rank, LeaderboardRowController);
        if (!rowController) {
          this.owner.createLeaderboardRowController(rank, item);
          rowController = this.owner.getChild(rank, LeaderboardRowController);
        }
        if (!rowController) {
          return;
        }

        const playerId = item.id;
        const previousPlayerId = rowController.playerId.value;
        let change: PlayerChange;

        if (!previousPlayerId || previousPlayerId === playerId) {
          // No player was at this rank previously (start up), or it's the same player
          change = "none";
        } else {
          // The player that occupies this rank has changed
          removedPlayers.add(previousPlayerId);
          // If the player has been removed already, then he has fallen rank, otherwise he has risen
          change = removedPlayers.has(playerId) ? "falling" : "rising";
        }
        rowController.updateRow(item, rank, change);
      });

    },
  })
  readonly leaderboardDownlink!: ValueDownlink<this>;
}
