import { DataPointView, GraphView, LinePlotView } from "@swim/chart";
import { HtmlView } from "@swim/dom";
import { CanvasView } from "@swim/graphics";
import { ViewRef } from "@swim/view";
import { PlayerChange } from "../types";
import { Timing } from "@swim/util";

export class KdSeriesView extends HtmlView {
  DEFAULT_STROKE: string = "#FBFBFB";
  ELECTRIC_TEAL: string = "#66FFDD";
  TANGERINE_SCREAM: string = "#F8A147";

  constructor() {
    const div = document.createElement("div");
    div.classList.add("kdSeriesContainer");
    super(div);
    this.containerView.setView(div);
    this.plotView.insertView(this.graphView.insertView(this.canvasView.insertView(this)));
  }

  addPoint(x?: number, y?: number) {
    this.plotView.attachView().insertDataPoint(new DataPointView(x, y));
  }

  updateStroke(change: PlayerChange): void {
    const newStroke =
      change === "none"
        ? this.DEFAULT_STROKE
        : change === "rising"
        ? this.ELECTRIC_TEAL
        : this.TANGERINE_SCREAM;
    this.plotView
      .attachView()
      .set({ stroke: newStroke }, Timing.fromInit({ easing: "linear", t0: 0, dt: 500 }));
  }

  @ViewRef({
    viewType: HtmlView,
    initView(view): void {
      view.set({
        style: {
          display: "flex",
          width: "90%",
          height: "67%",
          touchAction: "none",
          userSelect: "none",
        },
      });
    },
  })
  readonly containerView!: ViewRef<this, HtmlView>;

  @ViewRef({
    viewType: CanvasView,
    createView(): CanvasView {
      return CanvasView.create().set({
        style: {
          touchAction: "manipulation",
        },
        wheelEvents: true,
        mouseEvents: true,
        pointerEvents: true,
        touchEvents: true,
      });
    },
  })
  readonly canvasView!: ViewRef<this, CanvasView>;

  @ViewRef({
    viewType: GraphView,
    createView(): GraphView {
      const graphView = GraphView.create();
      graphView.domainTracking(true);

      return graphView;
    },
  })
  readonly graphView!: ViewRef<this, GraphView>;

  @ViewRef({
    viewType: LinePlotView,
    createView(): LinePlotView {
      const plotView = LinePlotView.create().set({
        hitMode: "plot",
        stroke: this.owner.DEFAULT_STROKE,
        strokeWidth: 1,
      });

      return plotView;
    },
  })
  readonly plotView!: ViewRef<this, LinePlotView>;
}
